import React from 'react';
import { MainContainer, HeadGear } from 'Templates';
import { CommonSetting } from '../../CommonSetting';
import { localkey } from 'Source/App/Main/local-config';
import * as Section from './Sections';
import * as Table from './Tables';

const Collapse = () => { 
	const title = 'Collapse';
	const context = `it is toggable to expand and hide content.`;

	return (
		<>  
            <HeadGear>
                <HeadGear.Meta 
					title={`${title} - Neumorphism Project | Mytabworks`}
					description={context}
					image='/images/icons/icon-512x512.png'
				/>
            </HeadGear>
			<MainContainer 
				title={title}
				context={context}
				main={<CollapseMain />}
				side={<CollapseSidePanel />}
				style={{ backgroundColor: 'transparent' }}
			/>
		</>
	);
};

Collapse.propTypes = {};

const CollapseMain = () => (
	<>
		<Section.Example /> 
		<Section.ShadowLess />
		<Section.Accordion />
		<Section.Javascript /> 
		<Table.Methods />
		<Table.Events />
		<Section.Events />
	</>
);

const CollapseSidePanel = () => (
	<ul className='scroll-nav'>
		<li>
			<h6 className='px-3 py-2 mb-0'>Collapse Style</h6>
		</li>
		<CommonSetting
			localkey={localkey.collapse}
			settingsAllowed={['distance', 'blur', 'intensity', 'radius']}
			allowEngrave
		/>
	</ul>
);

export default Collapse;
