import React, { useEffect } from 'react';
import { Section, CodeReadOnly, HoverCopy } from 'Templates';
import * as code from './StringCodes';
import N from '../../scripts';

const lorem =
	'Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation ullamco laboris nisi ut aliquip ex ea commodo consequat. Duis aute irure dolor in reprehenderit in voluptate velit esse cillum dolore eu fugiat nulla pariatur. Excepteur sint occaecat cupidatat non proident, sunt in culpa qui officia deserunt mollit anim id est laborum.';
export const Example = () => {
	useEffect(() => {
		const element = document.querySelector('#example .n-collapse');
		const instance = N.collapse.getInstance(element);
		return () => instance.destroy();
	}, []);
	return (
		<Section
			title='Example'
			context='.n-collapse class is just a wrapping element which is not advisable to put padding on it and will remain as is. put .show class to display it by default.'>
			<HoverCopy code={code.Example}>
				<CodeReadOnly code={code.Example} />
				<button
					className='n-btn mb-2'
					data-collapse='#collapse-live'
					type='button'
                    aria-expanded='true'>
					Button with data-collapse
				</button>
                <a 
                    className='n-btn mb-2 ml-2' 
                    href='#collapse-live'
                    role='button' 
                    aria-expanded='true'>
                    Anchor with href
                </a>
				<div id='collapse-live' className='n-collapse show'>
					<div className='n-collapse-content'>
						<p>{lorem}</p>
						<button
							className='n-btn mb-2'
							data-collapse='#collapse-live'
							type='button'
                            aria-expanded='true'>
							close
						</button>
					</div>
				</div>
			</HoverCopy>
		</Section>
	);
};

export const ShadowLess = () => {
	useEffect(() => {
		const element = document.querySelector('#no-shadow .n-collapse');
		const instance = N.collapse.getInstance(element);
		return () => instance.destroy();
	}, []);
	return (
		<Section
			title='No shadow'
			context={
				<>
					to remove the default collapse shadow, just add attribute{' '}
					<b>data-shadow="false"</b>.
				</>
			}> 
			<HoverCopy code={code.ShadowLess}>
                <CodeReadOnly code={code.ShadowLess} />
				<div className='n-card'>
					<div className='n-card-header' data-collapse='#collapse-sl' aria-expanded='false'>
						Card
					</div>
					<div className='n-collapse' data-shadow='false' id='collapse-sl'>
						<div className='n-collapse-content'>
							<div className='n-card-body'>
								<p>{lorem}</p>
							</div>
						</div>
					</div>
				</div>
			</HoverCopy>
		</Section>
	);
};
export const Accordion = () => {
	useEffect(() => {
		const elements = Array.from(
			document.querySelectorAll('#accordion .n-collapse')
		);
		const instances = elements.map((element) =>
			N.collapse.getInstance(element)
		);
		return () => instances.forEach((instance) => instance.destroy());
	}, []);
	return (
		<Section
			title='Accordion'
			context={
				<>
					by adding attribute <b>data-parent="#collapse-parent"</b> on each{' '}
					<b>.n-collpase</b> to implement accordion collapse.
				</>
			}>
			<HoverCopy code={code.Accordion}>
				<CodeReadOnly code={code.Accordion} />
				<div id='collapse-parent'>
					<div className='n-card mb-2'>
						<div className='n-card-header' data-collapse='#collapse-1' aria-expanded='false'>
							Card 1
						</div>
						<div
							id='collapse-1'
							className='n-collapse'
							data-shadow='false'
							data-parent='#collapse-parent'>
							<div className='n-collapse-content'>
								<div className='n-card-body'>
									<p>{lorem}</p>
								</div>
							</div>
						</div>
					</div>
					<div className='n-card mb-2'>
						<div className='n-card-header' data-collapse='#collapse-2' aria-expanded='false'>
							Card 2
						</div>
						<div
							id='collapse-2'
							className='n-collapse'
							data-shadow='false'
							data-parent='#collapse-parent'>
							<div className='n-collapse-content'>
								<div className='n-card-body'>
									<p>{lorem}</p>
								</div>
							</div>
						</div>
					</div>
					<div className='n-card'>
						<div className='n-card-header' data-collapse='#collapse-3' aria-expanded='false'>
							Card 3
						</div>
						<div
							id='collapse-3'
							className='n-collapse'
							data-shadow='false'
							data-parent='#collapse-parent'>
							<div className='n-collapse-content'>
								<div className='n-card-body'>
									<p>{lorem}</p>
								</div>
							</div>
						</div>
					</div>
				</div>
			</HoverCopy>
		</Section>
	);
};

export const Javascript = () => (
	<Section
		title='Javascript'
		context='to make it working you need a little script.'>
		<HoverCopy code={code.Javascript}>
			<CodeReadOnly code={code.Javascript} />
		</HoverCopy>
	</Section>
);

export const Events = () => (
	<Section>
		<HoverCopy code={code.Events}>
			<CodeReadOnly code={code.Events} />
		</HoverCopy>
	</Section>
);
