import React from 'react';
import PropTypes from 'prop-types';
import { useLocal } from 'Source/App/Storage/Local';
import { Tooltip } from 'Snippets/Tooltip'

export const CommonSetting = ({
	settingsAllowed,
	localkey,
	allowConvex,
	allowEngrave,
	allowEngraveWithConcave,
	allowFloat,
	max,
	min
}) => {
	const { getLocal, setLocal } = useLocal(localkey);
	const settings = getLocal();
	max = {distance: 10, blur: 20, intensity: 100, radius: 50, border: 10, ...max}
	min = {distance: 1, blur: 2, intensity: 10, radius: 0, border: 1, ...min}
	return (
		<>
			{allowConvex && (
				<li>
					<div className='px-3 py-2'>
						<div className='n-switch'>
							<input
								id='switch-convex'
								type='checkbox'
								checked={settings.convex}
								onChange={(e) => {
									const value = 2;
									setLocal((prev) => ({
										...prev,
										convex: !prev.convex,
										distance: value,
										blur: value * 2,
									}));
								}}
							/>
							<label className='n-switch-label' htmlFor='switch-convex'>convex</label>
						</div>
					</div>
				</li>
			)}
			{allowEngrave && (
				<li>
					<div className='px-3 py-2'>
						<div className='n-switch'>
							<input
								id='switch-engrave'
								type='checkbox'
								checked={settings.engrave}
								onChange={(e) => {
									setLocal((prev) => ({
										...prev, 
										engrave: !prev.engrave
									}));
								}}
							/>
							<label className='n-switch-label' htmlFor='switch-engrave'>engrave</label>
						</div>
					</div>
				</li>
			)}
			{allowEngraveWithConcave && (
				<li>
					<div className='px-3 py-2'>
						<div className='n-switch'>
							<input
								id='switch-engrave'
								type='checkbox'
								checked={settings.engrave}
								onChange={(e) => {
									setLocal((prev) => ({
										...prev,
										concave: prev.engrave,
										engrave: !prev.engrave,
										border: 2,
									}));
								}}
							/>
							<label className='n-switch-label' htmlFor='switch-engrave'>engrave</label>
						</div>
					</div>
				</li>
			)}
			{allowFloat && (
				<li>
					<div className='px-3 py-2'>
						<div className='n-switch'>
							<input
								id='switch-float'
								type='checkbox'
								checked={settings.float}
								onChange={(e) => {
									setLocal((prev) => ({
										...prev,
										float: !prev.float,
									}));
								}}
							/>
							<label className='n-switch-label' htmlFor='switch-float'>float</label>
						</div>
					</div>
				</li>
			)}
			{settingsAllowed.map((each) => (
				<li key={each}>
					<div className='px-3 py-2'>
						<label>{each}</label>
						<Tooltip variant='dark' message={`${settings[each]}`} placement="left">
							<input
								type='range'
								value={settings[each]}
								min={min[each]}
								max={max[each]}
								className='n-form-control'
								onChange={(e) => {
									const value = parseInt(e.target.value);
									setLocal((prev) =>
										each === 'distance'
											? {
													...prev,
													distance: value,
													blur: value * 2,
											}
											: { ...prev, [each]: value }
									);
								}}
							/>
						</Tooltip>
					</div>
				</li>
			))}
		</>
	);
};

CommonSetting.propTypes = {
	localkey: PropTypes.string,
	settingsAllowed: PropTypes.arrayOf(
		PropTypes.oneOf(['distance', 'blur', 'intensity', 'radius', 'border'])
	),
};
