import React from 'react';
import { Section, Table } from 'Templates';

const MethodsRows = [
	{
		method: 'Instance.show',
		return: 'void', 
		description: 'to show the collapse programmatically.',
	},
	{
		method: 'Instance.close',
		return: 'void', 
		description: 'to close the collapse programmatically.', 
	},
	{
		method: 'Instance.destroy',
		return: 'void', 
		description: 'it is use to destroy and kill the collapse instance.',
	},
]

const EventRows = [
    {
		event: 'show.n.collapse', 
		description: 'This event fires immediately when the show instance method is called.',
    },
    {
		event: 'shown.n.collapse', 
		description: 'This event fires when the collapse is completely shown (CSS transition is complete).',
    },
    {
		event: 'hide.n.collapse', 
		description: 'This event fires immediately when the close instance method is called.',
    },
    {
		event: 'hidden.n.collapse', 
		description: 'This event fires when the collapse is completely hidden (CSS transition is complete).',
    },
]

export const Methods = () => (
	<Section
		title='Methods'>
		<Table data={MethodsRows} />
	</Section>
);

export const Events = () => (
	<Section
		title='Events'
        context={<>All collapse events are fired at the <b>.n-collapse</b> element.</>}>
		<Table data={EventRows} />
	</Section>
);